
export const errMessage = {
  NotMetaMask: 'Please install MetaMask',
  ExpMetaMask: 'MetaMask not detected',
  ChangeMetaMask: 'Please switch MetaMask to Ethereum Mainnet',
  MintStage: 'Anomaly detected, please reset MetaMask and try again',
  CancelSignature: 'Transaction canceled',
  UnKnownExp: 'Unknown error',
  NonceTooHigh: 'You\'re too quick! Take a breather.',
  LackofBalance: 'Insufficient balance, forging not available at the moment',
  WaitWallet: 'Wallet window is open, please complete the connection operation within the wallet window',
  AddressGas: 'Insufficient balance',
  TransactionError: 'Transaction failed!',
  UserNotValid: 'Invalid user',
  TooMuchToBuyShare: 'Purchase amount too large',
  TooSmallToBuyShare: 'Purchase amount too small',
  OnePrediction: 'You can predict only once.',
  Predictionfinished: 'Prediction has not finished',
  AfterSometime: "Try again after some time",
  PredictionFinished: 'Prediction finished',
  NotBeenAuthorized: 'The requested method and/or account has not been authorized by the user.',
};

export function expMessage(errInput) {
  if (errInput.includes('User rejected the request') === true) {
    return errMessage.ChangeMetaMask;
  } else if (
    errInput.includes('User denied transaction signature') === true ||
    errInput.includes('user rejected transaction') === true
  ) {
    return errMessage.CancelSignature;
  } else if (errInput.includes('Nonce too high') === true) {
    return errMessage.NonceTooHigh;
  } else if (errInput.includes('money not enough') === true) {
    return errMessage.LackofBalance;
  } else if (errInput.includes('wallet_requestPermissions') === true) {
    return errMessage.WaitWallet;
  } else if (errInput.includes('eth_requestAccounts') === true) {
    return errMessage.WaitWallet;
  } else if (errInput.includes('insufficient funds for gas * price + value') === true) {
    return errMessage.AddressGas;
  } else if (errInput.includes('transaction failed') === true) {
    return errMessage.TransactionError;
  } else if (errInput.includes('Unrecognized chain') === true) {
    return errMessage.NotMetaMask;
  } else if (errInput.includes('user not valid') === true) {
    return errMessage.UserNotValid;
  } else if (errInput.includes('too much to buy share') === true) {
    return errMessage.TooMuchToBuyShare;
  } else if (errInput.includes('too small to buy share') === true) {
    return errMessage.TooSmallToBuyShare;
  } else if (errInput.includes('You can only make one prediction') === true) {
    return errMessage.OnePrediction;
  } else if (errInput.includes('Prediction has not finished') === true) {
    return errMessage.Predictionfinished;
  } else if (errInput.includes('Try again after some time') === true) {
    return errMessage.AfterSometime;
  } else if (errInput.includes('Prediction finished') === true) {
    return errMessage.PredictionFinished;
  } else if (errInput.includes('account has not been authorized by the user') === true) {
    return errMessage.NotBeenAuthorized;
  } else {
    return errMessage.UnKnownExp;
  }

};
