import React, { useEffect, useState } from "react";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { ethers } from "ethers";

import "./index.css";
import config from "../../config/global.json";

import { errMessage, expMessage } from "../../utils/error";
import { warn, success } from "../../utils/toast";
import { formattedDateTimestamp, formatDatetime } from "../../utils/tools";

import contractAbi from "../../contracts/PredictionContract.json";

let contract = null;
let wallet = "";
let contract_endTime = "";
let contract_lottery = 0;

function Root() {
  useEffect(() => {
    try {
      const { ethereum } = window;
      if (ethereum) {
        window.ethereum.on("accountsChanged", (newAccounts) => {
          goDisconnectWallet();
        });
        window.ethereum.on("chainChanged", (newChainId) => {
          if (config.chainId !== newChainId) {
            goDisconnectWallet();
          }
        });
      } else {
        warn("Metamask not detected!");
      }
    } catch (error) {
      warn(`${expMessage(error.message)}`);
    }
  }, []);

  const [isLogin, setIsLogin] = useState(false);
  const [endTime, setEndTime] = useState("");
  const [lottery, setLottery] = useState("1");
  const [all, setAll] = useState(false);
  const [modalShow, setModalShow] = useState(false);
  const [modalText, setModalText] = useState("");

  async function goConnectWallet() {
    window.console.log("goConnectWallet=>1=>");
    try {
      const { ethereum } = window;
      if (ethereum) {
        const chainId = await ethereum.request({ method: "eth_chainId" });
        window.console.log("goConnectWallet=>chainId=>2=>", chainId);
        if (chainId !== config.chainId) {
          await ethereum.request({
            method: "wallet_switchEthereumChain",
            params: [{ chainId: config.chainId }],
          });
        }
        await ethereum
          .request({ method: "eth_requestAccounts" })
          .then(async (accounts) => {
            const selectAccount = String(accounts[0]).toLocaleLowerCase();
            window.console.log(
              "goConnectWallet=>accounts[0]=>3=>",
              selectAccount
            );
            window.localStorage.setItem("address", selectAccount);
            wallet = selectAccount;
            setModalText("Loading...");
            setModalShow(true);
            setIsLogin(true);
            await initContract(ethereum);
          });
      } else {
        warn(`${errMessage.NotMetaMask}`);
      }
    } catch (err) {
      window.console.log(err.message);
      warn(`${expMessage(err.message)}`);
    }
  }

  async function initContract(ethereum) {
    window.console.log("initContract=>5=>");
    const newProvider = new ethers.providers.Web3Provider(ethereum, "any");
    if (newProvider) {
      window.console.log("initContract=>6=>");
      const newContract = new ethers.Contract(
        config.contractAddress,
        contractAbi.abi,
        newProvider.getSigner()
      );
      contract = newContract;
      await loadData();
    }
  }

  async function loadData() {
    window.console.log("loadData=>7=>");
    await getPredictionResult();
  }

  async function getPredictionResult() {
    window.console.log(
      "getPredictionResult=>8=>contract=>",
      contract,
      "wallet=>",
      wallet
    );

    try {
      if (contract) {
        const blockEndTime = await contract.endTime();
        contract_endTime = Number(blockEndTime);
        setEndTime(formattedDateTimestamp(Number(contract_endTime)));
        const lotteryResult = await contract.lotteryResult();
        contract_lottery = Number(lotteryResult);
        setLottery(contract_lottery);
        console.log(
          "contract_endTime:",
          contract_endTime,
          "contract_lottery:",
          contract_lottery
        );
        setModalShow(false);
      }
    } catch (error) {
      window.console.error("getPredictionResult=>err=>", error.message);
      warn(`${expMessage(error.message)}`);
    }
  }

  async function setTime() {
    if (endTime.length > 0) {
      window.console.log("setTime=>yes", formatDatetime(endTime));
      try {
        if (contract) {
          const result = await contract.setEndTime(
            Number(formatDatetime(endTime) / 1000)
          );
          setModalText("Loading...");
          setModalShow(true);
          console.log("contract.setEndTime:", result);
          if (result.hash) {
            setModalShow(false);
            await loadData();
            success("!");
          }
        }
      } catch (error) {
        window.console.error("setEndTime=>err=>", error.message);
        warn(`${expMessage(error.message)}`);
      }
    } else {
      window.console.log("setTime=>no", formatDatetime(endTime));
      warn("!");
    }
  }

  async function setLotteryResult() {
    window.console.log("setLotteryResult=>", lottery);
    try {
      if (contract) {
        const result = await contract.setLotteryResult(Number(lottery));
        setModalText("Loading...");
        setModalShow(true);
        console.log("contract.setLotteryResult:", result);
        if (result.hash) {
          setModalShow(false);
          await loadData();
          success("!");
        }
      }
    } catch (error) {
      window.console.error("setLotteryResult=>err=>", error.message);
      warn(`${expMessage(error.message)}`);
    }
  }

  async function withdrawTax() {
    window.console.log("withdrawTax=>");
    try {
      if (contract) {
        const result = await contract.withdrawTax(Boolean(all));
        setModalText("Loading...");
        setModalShow(true);
        console.log("contract.withdrawTax:", result);
        if (result.hash) {
          setModalShow(false);
          await loadData();
          success("!");
        }
      }
    } catch (error) {
      window.console.error("withdrawTax=>err=>", error.message);
      warn(`${expMessage(error.message)}`);
    }
  }

  async function resetData() {
    window.console.log("resetData=>");
    try {
      if (contract) {
        const result = await contract.resetData();
        setModalText("Loading...");
        setModalShow(true);
        console.log("contract.resetData:", result);
        if (result.hash) {
          setModalShow(false);
          await loadData();
          success("!");
        }
      }
    } catch (error) {
      window.console.error("resetData=>err=>", error.message);
      warn(`${expMessage(error.message)}`);
    }
  }

  async function goDisconnectWallet() {
    window.console.log("goDisconnectWallet=>");
    window.localStorage.clear();
    wallet = "";
    setIsLogin(false);
  }

  return (
    <div className="page">
      <div className="app">
        {isLogin === true ? (
          <div className="boxs">
            <div className="title"></div>
            <div className="content">
              <label></label>
              <p>
                <input
                  type="datetime-local"
                  className="input"
                  value={endTime}
                  onChange={(e) => {
                    console.log("setEndTime:", e.target.value);
                    setEndTime(e.target.value);
                  }}
                />
                <button className="button" onClick={setTime}>
                  
                </button>
              </p>
            </div>
            <div className="title"></div>
            <div className="content">
              {Number(new Date().getTime() / 1000) > contract_endTime ? (
                <div>
                  <label></label>
                  <p>
                    <select
                      className="input"
                      value={lottery}
                      onChange={(e) => setLottery(e.target.value)}
                    >
                      <option value="0"></option>
                      <option value="1"></option>
                      <option value="2"></option>
                    </select>
                    <button className="button" onClick={setLotteryResult}>
                      
                    </button>
                  </p>
                </div>
              ) : (
                <div className="pad"></div>
              )}
              {contract_lottery > 0 ? (
                <div>
                  <label></label>
                  <p>
                    <select
                      className="input"
                      value={all}
                      onChange={(e) => setAll(e.target.value)}
                    >
                      <option value="false"></option>
                      <option value="true"></option>
                    </select>
                    <button className="button" onClick={withdrawTax}>
                      
                    </button>
                    <button className="button" onClick={resetData}>
                      
                    </button>
                  </p>
                </div>
              ) : (
                <div></div>
              )}
            </div>
          </div>
        ) : (
          <div className="pad">
            <div className="metamask-button" onClick={goConnectWallet}>
              <img
                className="metamask-icon"
                src="../images/metamask-icon.png"
                alt="MetaMask Icon"
              />
              
            </div>
          </div>
        )}
      </div>
      <ToastContainer />
      <div
        className="modal-layer"
        style={{ display: modalShow === true ? "block" : "none" }}
      ></div>
      <div
        className="modal"
        style={{ display: modalShow === true ? "block" : "none" }}
      >
        <div className="modal-text">{modalText}</div>
      </div>
    </div>
  );
}

export default Root;
