import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Helmet } from "react-helmet";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import copy from "clipboard-copy";
import { BigNumber, ethers } from "ethers";
import { useTranslation } from "react-i18next";

import "./index.css";
import config from "../../config/global.json";
import lng from "../../config/languages.json";

import { errMessage, expMessage } from "../../utils/error";
import { warn, info, success, error } from "../../utils/toast";
import { formatWalletAddress, formattedDateTimestamp } from "../../utils/tools";

import contractAbi from "../../contracts/PredictionContract.json";

let contract = null;
let wallet = "";
let betAmount = 0.1;
let endTime = "";
let lottery = 0;

function Main() {
  useEffect(() => {
    try {
      const { ethereum } = window;
      if (ethereum) {
        window.ethereum.on("accountsChanged", (newAccounts) => {
          goDisconnectWallet();
        });
        window.ethereum.on("chainChanged", (newChainId) => {
          if (config.chainId !== newChainId) {
            goDisconnectWallet();
          }
        });
      } else {
        warn("Metamask not detected!");
      }
    } catch (error) {
      warn(`${expMessage(error.message)}`);
    }
  }, []);

  const navigate = useNavigate();
  const [isLogin, setIsLogin] = useState(false);
  const [predictionList, setPredictionList] = useState([]);
  const [balance, setBalance] = useState(0.0);
  const [total, setTotal] = useState(0.0);
  const [rise, setRise] = useState(0);
  const [riseNumber, setRiseNumber] = useState(0);
  const [fall, setFall] = useState(0);
  const [fallNumber, setFallNumber] = useState(0);
  const [modalShow, setModalShow] = useState(false);
  const [modalText, setModalText] = useState("");
  const { t, i18n } = useTranslation();
  const languageList = lng.languages;
  const strLng =
    localStorage.getItem("language") || navigator.language.split(/[-_]/)[0];
  const [checkedLan, setCheckedLan] = useState(strLng);

  async function goConnectWallet() {
    window.console.log("goConnectWallet=>1=>");
    try {
      const { ethereum } = window;
      if (ethereum) {
        const chainId = await ethereum.request({ method: "eth_chainId" });
        window.console.log("goConnectWallet=>chainId=>2=>", chainId);
        if (chainId !== config.chainId) {
          await ethereum.request({
            method: "wallet_switchEthereumChain",
            params: [{ chainId: config.chainId }],
          });
        }
        await ethereum
          .request({ method: "eth_requestAccounts" })
          .then(async (accounts) => {
            const selectAccount = String(accounts[0]).toLocaleLowerCase();
            window.console.log(
              "goConnectWallet=>accounts[0]=>3=>",
              selectAccount
            );
            window.localStorage.setItem("address", selectAccount);
            wallet = selectAccount;
            setModalText("Loading...");
            setModalShow(true);
            setIsLogin(true);
            const weiBalance = await ethereum.request({
              method: "eth_getBalance",
              params: [selectAccount],
            });
            const etherBalance = ethers.utils.formatEther(weiBalance);
            setBalance(etherBalance);

            window.console.log(
              "goConnectWallet=>etherBalance=>4=>",
              etherBalance
            );
            await initContract(ethereum);
          });
      } else {
        warn(`${errMessage.NotMetaMask}`);
      }
    } catch (err) {
      window.console.log(err.message);
      warn(`${expMessage(err.message)}`);
    }
  }

  async function initContract(ethereum) {
    window.console.log("initContract=>5=>");
    const newProvider = new ethers.providers.Web3Provider(ethereum, "any");
    if (newProvider) {
      window.console.log("initContract=>6=>");
      const newContract = new ethers.Contract(
        config.contractAddress,
        contractAbi.abi,
        newProvider.getSigner()
      );
      contract = newContract;
      await loadData();
    }
  }

  async function loadData() {
    window.console.log("loadData=>7=>");
    await getPredictionResult();
    await getHistory();
  }

  async function goDisconnectWallet() {
    window.console.log("goDisconnectWallet=>");
    window.localStorage.clear();
    wallet = "";
    setIsLogin(false);
  }

  async function getHistory() {
    window.console.log(
      "getHistory=>9=>contract=>",
      contract,
      "wallet=>",
      wallet
    );

    try {
      if (contract) {
        const result = await contract.getPredictionByAddress(wallet);
        window.console.log("getHistory=>10=>result=>", result);
        let blockHistoryList = {
          predictionTime: formattedDateTimestamp(Number(result.predictionTime)),
          winningResult: Number(result.predictionResult),
          winningAmount: parseFloat(
            ethers.utils.formatEther(BigNumber.from(result.predictionAmount))
          ),
          withdrawn: Boolean(result.hasWithdrawn),
        };
        if (result.predictor.toLocaleLowerCase() === wallet) {
          window.console.log("getHistory=>11=>result=>", blockHistoryList);
          setPredictionList([blockHistoryList]);
        } else {
          setPredictionList([]);
        }
        setModalShow(false);
      }
    } catch (error) {
      window.console.error("getHistory=>err=>", error.message);
      warn(`${expMessage(error.message)}`);
    }
  }

  async function getPredictionResult() {
    window.console.log(
      "getPredictionResult=>8=>contract=>",
      contract,
      "wallet=>",
      wallet
    );

    try {
      if (contract) {
        const result = await contract.getPredictionResults();
        const buyPrice = await contract.buyPrice();
        const blockEndTime = await contract.endTime();
        endTime = Number(blockEndTime);
        const lotteryResult = await contract.lotteryResult();
        lottery = Number(lotteryResult);

        console.log(
          "buyPrice:",
          buyPrice,
          "endTime:",
          endTime,
          "lottery:",
          lottery
        );

        if (
          ethers.utils.formatEther(BigNumber.from(buyPrice)).toString() ===
          betAmount.toString()
        ) {
          console.log("Contract method result:", result);
          const totalAmount = parseFloat(Number(result[0]) * betAmount);
          setTotal(totalAmount.toFixed(2));
          const riseNumber =
            (Number(result[1]._hex) / Number(result[0]._hex)) * 100;
          setRiseNumber(Number(result[1]._hex));
          setRise(riseNumber.toFixed(2));
          const fallNumber =
            (Number(result[2]._hex) / Number(result[0]._hex)) * 100;
          setFall(fallNumber.toFixed(2));
          setFallNumber(Number(result[2]._hex));
        } else {
          error(`Abnormal data...`);
        }
      }
    } catch (error) {
      window.console.error("getPredictionResult=>err=>", error.message);
      warn(`${expMessage(error.message)}`);
    }
  }

  async function goWithdraw() {
    window.console.log("goWithdraw=>");
    try {
      if (contract) {
        setModalText("Withdrawing funds currently...");
        setModalShow(true);
        const result = await contract.withdraw();
        window.console.log("goWithdraw=>result=>", result);
        if (result.hash) {
          contract.on("WithdrawEvent", eventWithdraw);
        }
      }
    } catch (error) {
      window.console.error("goWithdraw=>err=>", error.message);
      warn(`${expMessage(error.message)}`);
      setModalShow(false);
    }
  }

  async function eventWithdraw(preditionData, bonus, timeStamp) {
    window.console.log("Event eventWithdraw:", preditionData, bonus, timeStamp);
    setModalShow(false);
    success(t("message.withdrawalSuccessful", "Withdrawal successful!"));
    await loadData();
  }

  async function goRise() {
    window.console.log("goRise=>");
    await goBuy(1);
  }

  async function goFall() {
    window.console.log("goFall=>");
    await goBuy(2);
  }

  async function goBuy(predictionResult) {
    window.console.log("goBuy=>");
    try {
      if (contract) {
        setModalText(
          t(
            "message.onBlockchain",
            "Currently placing a bet on the blockchain..."
          )
        );
        setModalShow(true);
        const result = await contract.recordPrediction(predictionResult, {
          value: ethers.utils.parseEther(String(betAmount)),
        });
        window.console.log("goBuy=>result=>", result);
        if (result.hash) {
          contract.on("PredictionRecorded", eventPredictionRecorded);
        }
      }
    } catch (error) {
      window.console.error("goBuy=>err=>", error.message);
      warn(`${expMessage(error.message)}`);
      setModalShow(false);
    }
  }

  async function eventPredictionRecorded(
    from,
    timestamp,
    msg,
    predictionResult
  ) {
    window.console.log(
      "Event eventPredictionRecorded:",
      from,
      timestamp,
      msg,
      predictionResult
    );
    if (Number(predictionResult) === 1) {
      info(t("message.forecastedRise", "You forecasted a rise"));
    } else {
      info(t("message.forecastedFall", "You forecasted a fall"));
    }
    setModalShow(false);
    await loadData();
  }

  function goShareTwitter() {
    window.console.log("goShareTwitter=>");
    const url = `https://twitter.com/intent/tweet?url=${window.location.href}&text=${config.siteTitle}`;
    window.open(url, "_blank");
  }

  function goShareFacebook() {
    window.console.log("goShareFacebook=>");
    const url = `https://www.facebook.com/sharer/sharer.php?u=${window.location.href}&text=${config.siteTitle}`;
    window.open(url, "_blank");
  }

  function goBookmarkPage() {
    window.console.log("goBookmarkPage=>");
    var pageTitle = document.title;
    var pageURL = window.location.href;
    if (window.sidebar && window.sidebar.addPanel) {
      // Firefox
      window.sidebar.addPanel(pageTitle, pageURL, "");
    } else if (window.external && "AddFavorite" in window.external) {
      // Internet Explorer
      window.external.AddFavorite(pageURL, pageTitle);
    } else if (window.opera && window.print) {
      // Opera
      var elem = document.createElement("a");
      elem.setAttribute("href", pageURL);
      elem.setAttribute("title", pageTitle);
      elem.setAttribute("rel", "sidebar");
      elem.click();
    } else {
      // Other
      warn(
        "Press " +
          (navigator.userAgent.toLowerCase().indexOf("mac") !== -1
            ? "Cmd"
            : "Ctrl") +
          "+D to bookmark this page."
      );
    }
  }

  function goCopyToClipboard() {
    const pageURL = window.location.href;
    window.console.log("goCopyToClipboard=>", pageURL);
    copy(pageURL);
    success(
      t(
        "message.copyToClipboard",
        "Link copied to clipboard!Forward/Share with your crypto friends!"
      )
    );
  }

  function onChangeLanguage(e) {
    window.console.log("onChangeLanguage=>", e.target.value);
    i18n.changeLanguage(e.target.value);
    setCheckedLan(e.target.value);
    window.localStorage.setItem("language", e.target.value);
  }

  function goMore() {
    navigate("/more");
  }

  function renderLottery(lottery, winningResult) {
    if (lottery === 0) {
      return (
        <div className="item-row">
          {t("common.lottery1", "You predicted")}
          <span className="yellow">
            {winningResult === 1
              ? t("common.buttonRise", "Rise")
              : t("common.buttonFall", "Fall")}
          </span>
          {t("common.lottery2", ",Lottery result")}
          <span className="wait">
            {t("common.lottery3", "Waiting for the results.")}
          </span>
        </div>
      );
    } else {
      return (
        <div className="item-row">
          {t("common.lottery1", "You predicted")}
          <span className="yellow">
            {winningResult === 1
              ? t("common.buttonRise", "Rise")
              : t("common.buttonFall", "Fall")}
          </span>
          {t("common.lottery2", ",Lottery result")}
          <span className="blue">
            {lottery === 1
              ? t("common.buttonRise", "Rise")
              : t("common.buttonFall", "Fall")}
          </span>
          ,
          {winningResult === lottery ? (
            <span className="yes">{t("common.lottery4", "You winning!")}</span>
          ) : (
            <span className="no">
              {t("common.lottery5", "You did not win!")}
            </span>
          )}
        </div>
      );
    }
  }

  function renderWithdrawn(lottery, winningResult, winningAmount, withdrawn) {
    if (lottery === 0) {
      return (
        <div className="item-row">
          <div className="coin-wait">{`${winningAmount} ${config.coinPoint}`}</div>
          <div className="withdrawn"></div>
        </div>
      );
    } else {
      return (
        <div>
          {lottery === winningResult ? (
            <div className="item-row">
              <div className="coin-yes">
                +
                {`${
                  lottery === 1
                    ? (
                        (winningAmount * Number(total)) /
                        Number(riseNumber)
                      ).toFixed(4)
                    : (
                        (winningAmount * Number(total)) /
                        Number(fallNumber)
                      ).toFixed(4)
                } ${config.coinPoint}`}
              </div>
              <div className="withdrawn">
                {withdrawn === true ? (
                  <span>{t("common.completed", "Completed")}</span>
                ) : (
                  <button className="withdraw-button" onClick={goWithdraw}>
                    <span className="iconfont icon-withdraw"></span>
                    {t("common.withdrawn", "Withdraw")}
                  </button>
                )}
              </div>
            </div>
          ) : (
            <div className="item-row">
              <div className="coin-no">
                -{`${winningAmount} ${config.coinPoint}`}
              </div>
            </div>
          )}
        </div>
      );
    }
  }

  return (
    <div className="page">
      <Helmet>
        <title>
          {t("app.title", "Predict the weekly rise and fall of the Nasdaq.")}
        </title>
        <meta
          name="keywords"
          content={t(
            "app.keywords",
            "U.S. stock market, Nasdaq, cryptocurrencies, Bitcoin, Ethereum, predict the rise and fall of Nasdaq,Win Ethereum, Earn USD"
          )}
        />
        <meta
          name="description"
          content={t(
            "app.description",
            "Predict the weekly rise and fall of the Nasdaq,Win Ethereum, Earn US Dollars."
          )}
        />
        <meta name="author" content={config.author} />
        <meta name="robots" content="all" />
      </Helmet>
      <div className="app">
        {isLogin === true ? (
          <div className="topbar-header">
            <div className="tools">
              <div className="iconfont icon-twitter" onClick={goShareTwitter}>
                <div className="tooltip">
                  {t("tooltip.twitter", "Forward/Share on Twitter.")}
                </div>
              </div>
              <div className="iconfont icon-facebook" onClick={goShareFacebook}>
                <div className="tooltip">
                  {t("tooltip.facebook", "Forward/Share on Facebook.")}
                </div>
              </div>
              <div className="iconfont icon-share" onClick={goCopyToClipboard}>
                <div className="tooltip">
                  {t("tooltip.copy", "Forward/Share with your crypto friends.")}
                </div>
              </div>
              <div className="iconfont icon-favorite" onClick={goBookmarkPage}>
                <div className="tooltip">
                  {t(
                    "tooltip.bookmark",
                    "Bookmark it in your browser so you can easily find it."
                  )}
                </div>
              </div>
            </div>
            <div className="operations">
              <span
                className="wallet-button"
                title={t("tooltip.disconnect", "Disconnect Wallet")}
                onClick={goDisconnectWallet}
              >
                {formatWalletAddress(wallet)}
                <span className="iconfont icon-disconnect"></span>
              </span>
            </div>
          </div>
        ) : (
          <div></div>
        )}
        <header className="head">
          <div className="h">
            {t("app.title", "Predict the weekly rise and fall of the Nasdaq.")}
          </div>
          <div className="sub">
            {t("site.subTitle", "Win Ethereum, Earn US Dollars")}
          </div>
          {isLogin === true ? (
            <div>
              <div className="tips">
                {t("common.prizePool", "Current prize pool")}
                <span className="eth">{total}</span>
                {config.coinPoint}
              </div>
              <div className="message">
                {t(
                  "common.afterTips",
                  "After the closing, correct predictions will share the prize pool."
                )}
              </div>
            </div>
          ) : (
            <div>
              <div className="help">
                <div className="h2">{t("howtoPlay.title", "How to Play?")}</div>
                <div className="content">
                  <strong>
                    {t("howtoPlay.eligibilityTitle", "Eligibility")}
                  </strong>
                  <ol>
                    <li>
                      {t(
                        "howtoPlay.eligibility1",
                        "Interested in the cryptocurrency market and earning money."
                      )}
                    </li>
                    <li>
                      {t("howtoPlay.eligibility2", "Have a MetaMask wallet.")}
                    </li>
                    <li>
                      {t(
                        "howtoPlay.eligibility3",
                        "Want to earn USD and win Ethereum."
                      )}
                    </li>
                  </ol>
                  <strong>
                    {t("howtoPlay.predictionMethod", "Prediction Method")}
                  </strong>
                  <ol>
                    <li>
                      {t(
                        "howtoPlay.method1",
                        "Participants predict the rise or fall of the Nasdaq index before the Nasdaq market closes every Friday at 15:30 Eastern Time."
                      )}
                    </li>
                    <li>
                      {t(
                        "howtoPlay.method2",
                        "Participants can forecast whether the Nasdaq market will rise or fall by the closing time."
                      )}
                    </li>
                    <li>
                      {t("howtoPlay.method3", { coinPoint: config.coinPoint })}
                    </li>
                  </ol>
                  <strong>{t("howtoPlay.rewardRules", "Reward Rules")}</strong>
                  <ol>
                    <li>
                      {t(
                        "howtoPlay.rules1",
                        "Participants correctly predicting whether the Nasdaq market will rise or fall at closing will evenly share the Ethereum in the prize pool."
                      )}
                    </li>
                  </ol>
                  <h4>{t("howtoPlay.OtherTitle", "Other")}</h4>
                  <ol>
                    <li>
                      {t(
                        "howtoPlay.other1",
                        "Nasdaq closing data every Friday can be checked online on the Nasdaq official website."
                      )}
                    </li>
                    <li>
                      {t("howtoPlay.other2", { coinPoint: config.coinPoint })}
                    </li>
                    <li>
                      {t("howtoPlay.other3", { coinPoint: config.coinPoint })}
                    </li>
                  </ol>
                </div>
                <div className="metamask-button" onClick={goConnectWallet}>
                  <img
                    className="metamask-icon"
                    src="../images/metamask-icon.png"
                    alt="MetaMask Icon"
                  />
                  {t("common.connectWallet", "Connect MetaMask Wallet")}
                </div>
              </div>
              <div className="right-menus">
                <div className="li">
                  <select
                    className="select"
                    value={checkedLan}
                    onChange={onChangeLanguage}
                  >
                    {languageList.map((items, index) => {
                      return (
                        <option
                          key={`op_${index}`}
                          disabled={items.isused === 0 ? true : false}
                          value={items.value}
                        >
                          {items.text}
                        </option>
                      );
                    })}
                  </select>
                </div>
                <div className="li" onClick={goMore}>
                  {t("common.more", "More...")}
                </div>
              </div>
            </div>
          )}
        </header>
        {isLogin === true ? (
          <div>
            <section className="prediction-container">
              {lottery > 0 ? (
                <div>
                  <div className="prediction-message">
                    <div className="title">
                      {t("common.resultTitle", "Lottery result")}
                    </div>
                    <div className="result">
                      {lottery === 1 ? (
                        <div className="result-rise">
                          {t("common.resultRise", "Rise")}
                        </div>
                      ) : (
                        <div className="result-fall">
                          {t("common.resultFall", "Fall")}
                        </div>
                      )}
                    </div>
                    <div className="subtitle">
                      <a
                        href="https://www.nasdaq.com/"
                        target={"_blank"}
                        rel="noreferrer"
                      >
                        {t("common.nasdaqUrl", "Nasdaq official site")}
                      </a>
                    </div>
                  </div>
                </div>
              ) : (
                <div>
                  <div className="prediction-message">
                    <div className="title">
                      {t(
                        "common.deadlineTips",
                        "The prediction deadline is 3:30 PM this Friday."
                      )}
                    </div>
                    <div className="subtitle">
                      {formattedDateTimestamp(endTime)}
                    </div>
                  </div>
                  <div className="progress-container">
                    <div
                      className="progress-bar"
                      style={{ width: `${isNaN(rise) === true ? 0 : rise}%` }}
                    ></div>
                  </div>
                  {balance > betAmount ? (
                    <div className="prediction-options">
                      <div className="option-button rise" onClick={goRise}>
                        {t("common.buttonRise", "Rise")}{" "}
                        {isNaN(rise) === true ? 0 : rise}%{" "}
                        <span className="iconfont icon-rise"></span>
                      </div>
                      <div className="option-button fall" onClick={goFall}>
                        {t("common.buttonFall", "Fall")}{" "}
                        {isNaN(fall) === true ? 0 : fall}%{" "}
                        <span className="iconfont icon-fall"></span>
                      </div>
                    </div>
                  ) : (
                    <div className="prediction-options">
                      <div className="prediction-warn">
                        {t(
                          "common.balanceWarn",
                          "Your wallet balance is insufficient. Unable to participate in the prediction."
                        )}
                      </div>
                    </div>
                  )}
                </div>
              )}
            </section>
            <section className="participation-list">
              <div className="list-title">
                {t("common.recordTitle", "Prediction record")}
              </div>
              {predictionList.length > 0 ? (
                <div className="list-body">
                  {predictionList.map((row, index) => {
                    return (
                      <div className="list-item" key={`list-item-${index}`}>
                        <div className="item-row">
                          <span className="time">{row.predictionTime}</span>
                        </div>
                        {renderLottery(lottery, row.winningResult)}
                        {renderWithdrawn(
                          lottery,
                          row.winningResult,
                          row.winningAmount,
                          row.withdrawn
                        )}
                      </div>
                    );
                  })}
                </div>
              ) : (
                <div className="list-not">
                  <span className="iconfont icon-history"></span>
                  <br />
                  {t("common.recordEmpty", "Not prediction record!")}
                </div>
              )}
            </section>
          </div>
        ) : (
          <div></div>
        )}
      </div>
      <ToastContainer />
      <div
        className="modal-layer"
        style={{ display: modalShow === true ? "block" : "none" }}
      ></div>
      <div
        className="modal"
        style={{ display: modalShow === true ? "block" : "none" }}
      >
        <div className="modal-text">{modalText}</div>
      </div>
    </div>
  );
}

export default Main;
